"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import { api } from "@/lib/api";
import { toast } from "react-hot-toast";
import { 
  PackagePlus, Check, ArrowLeft, Loader2, 
  DollarSign, Layers, Tag, AlignLeft, Image as ImageIcon 
} from "lucide-react";
import Link from "next/link";
import { useQueryClient } from "@tanstack/react-query";

export default function CreateProductPage() {
  const queryClient = useQueryClient(); 
  const router = useRouter();
  const [loading, setLoading] = useState(false);
  const [formData, setFormData] = useState({
    name: "",
    description: "",
    pricePerUnit: "",
    minimumOrderQuantity: "",
    category: "",
    imageUrl: ""
  });

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    try {
      await api.post("/products", {
        ...formData,
        pricePerUnit: Number(formData.pricePerUnit),
        minimumOrderQuantity: Number(formData.minimumOrderQuantity),
        images: formData.imageUrl ? [formData.imageUrl] : []
      });
      queryClient.invalidateQueries({ queryKey: ["vendor-products"] });
      toast.success("Product listed successfully!");
      // Redirect to Inventory so they see the result
      router.push("/vendor/inventory"); 
    } catch (error: any) {
      toast.error(error.response?.data?.message || "Failed to create product");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-[#F8FAFC] p-6 md:p-12">
      <div className="max-w-3xl mx-auto">
        <Link href="/vendor/inventory" className="flex items-center text-slate-500 mb-8 hover:text-blue-600 transition-all group font-bold">
          <ArrowLeft size={20} className="mr-2 group-hover:-translate-x-1 transition-transform" /> 
          Back to Inventory
        </Link>

        <div className="bg-white rounded-[2.5rem] shadow-xl border border-slate-100 overflow-hidden">
          <div className="bg-slate-900 p-8 text-white text-center md:text-left">
            <h1 className="text-3xl font-black flex items-center justify-center md:justify-start gap-3 tracking-tight">
              <PackagePlus className="text-blue-400" size={32} /> List New Product
            </h1>
            <p className="text-slate-400 mt-2 font-medium">Add a new item to your marketplace catalog.</p>
          </div>

          <form onSubmit={handleSubmit} className="p-8 space-y-6">
            <div className="space-y-2">
              <label className="text-sm font-bold text-slate-700 ml-1">Product Name</label>
              <input required placeholder="Industrial Grade Steel..." className="w-full p-4 bg-slate-50 border border-slate-200 rounded-2xl outline-none focus:ring-4 focus:ring-blue-500/10 focus:border-blue-500 transition-all font-medium" 
                onChange={e => setFormData({...formData, name: e.target.value})} />
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div className="space-y-2">
                <label className="text-sm font-bold text-slate-700 ml-1 flex items-center gap-2">
                  <DollarSign size={14}/> Unit Price ($)
                </label>
                <input type="number" required placeholder="0.00" className="w-full p-4 bg-slate-50 border border-slate-200 rounded-2xl outline-none focus:ring-4 focus:ring-blue-500/10 focus:border-blue-500 transition-all font-medium" 
                  onChange={e => setFormData({...formData, pricePerUnit: e.target.value})} />
              </div>
              <div className="space-y-2">
                <label className="text-sm font-bold text-slate-700 ml-1 flex items-center gap-2">
                  <Layers size={14}/> Min Order Qty
                </label>
                <input type="number" required placeholder="100" className="w-full p-4 bg-slate-50 border border-slate-200 rounded-2xl outline-none focus:ring-4 focus:ring-blue-500/10 focus:border-blue-500 transition-all font-medium" 
                  onChange={e => setFormData({...formData, minimumOrderQuantity: e.target.value})} />
              </div>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div className="space-y-2">
                <label className="text-sm font-bold text-slate-700 ml-1 flex items-center gap-2">
                  <Tag size={14}/> Category
                </label>
                <input required placeholder="Electronics, Steel, etc." className="w-full p-4 bg-slate-50 border border-slate-200 rounded-2xl outline-none focus:ring-4 focus:ring-blue-500/10 focus:border-blue-500 transition-all font-medium" 
                  onChange={e => setFormData({...formData, category: e.target.value})} />
              </div>
              <div className="space-y-2">
                <label className="text-sm font-bold text-slate-700 ml-1 flex items-center gap-2">
                  <ImageIcon size={14}/> Image URL
                </label>
                <input placeholder="https://..." className="w-full p-4 bg-slate-50 border border-slate-200 rounded-2xl outline-none focus:ring-4 focus:ring-blue-500/10 focus:border-blue-500 transition-all font-medium text-sm" 
                  onChange={e => setFormData({...formData, imageUrl: e.target.value})} />
              </div>
            </div>

            <div className="space-y-2">
              <label className="text-sm font-bold text-slate-700 ml-1 flex items-center gap-2">
                <AlignLeft size={14}/> Description
              </label>
              <textarea rows={4} placeholder="Describe the quality, material, and lead times..." className="w-full p-4 bg-slate-50 border border-slate-200 rounded-2xl outline-none focus:ring-4 focus:ring-blue-500/10 focus:border-blue-500 transition-all font-medium" 
                onChange={e => setFormData({...formData, description: e.target.value})} />
            </div>

            <button disabled={loading} className="w-full py-5 bg-blue-600 text-white font-black rounded-2xl hover:bg-blue-700 transition-all flex items-center justify-center gap-2 shadow-xl shadow-blue-200 text-lg active:scale-95 disabled:opacity-50">
              {loading ? <Loader2 className="animate-spin" /> : <><Check size={20} /> Publish Listing</>}
            </button>
          </form>
        </div>
      </div>
    </div>
  );
}